export default function Input(props) {
  return (
    <div className="Input">
      <input type="text" />
      <style jsx>{`
        .Input {
          display: flex;
          align-items: center;
        }
        input {
          width: 100%;
          font-size: 24px;
          color: #4a4a4a;
          background-color: #f2f3f4;
          text-align: right;
          font-family: inherit;
          font-weight: 700;
          height: 50px;
          padding: 7.5px 15px;
          border-radius: 5px;
          border: none;
        }
        input:focus {
          outline: none;
        }
      `}</style>
    </div>
  )
}